<template>
  <div class="game-header">
    <h1>GUESS NUMBER GAME</h1>
    <p>Think of a number from 0 to 99! The AI will try to guess it in 5 tries!</p>
  </div>
<EndPopup :visible="isGameFinished" :hasPlayerWon="hasPlayerWon" :playersNumber="playersNumber" @triggerReplay="retryGame"/>
<!-- <button @click="changePopupVisibility">Change visibility of popup</button> -->
<GameComponent :showGame="hasGameStarted" :startNum ="startingNumber" @gameFinished="gameFinished"/>
<button class="rounded-corners" v-if="!hasGameStarted" @click="startGame">START!</button>
</template>

<script>
import EndPopup from './components/EndPopup.vue'
import GameComponent from './components/GameComponent.vue'

export default {
  name: 'App',
  components: {
    EndPopup,
    GameComponent
  },
  data(){
    return{
      hasPlayerWon: false,
      isGameFinished: false,
      hasGameStarted: false,
      startingNumber: 40,
      playersNumber: -1
    }
  },
  methods: {
    changePopupVisibility(){
      this.isGameFinished = !this.isGameFinished;
    },
    startGame(){
      this.startingNumber = 40 + Math.floor(Math.random()*20)
      console.log(this.startingNumber);
      this.hasGameStarted = true;
    },
    gameFinished(isPlayerWinner, winnerNumber){
      console.log("game finished");

      this.isGameFinished = true;
      this.hasPlayerWon = isPlayerWinner;
      this.hasGameStarted = false;
      if(winnerNumber)
        this.playersNumber = winnerNumber;
    },
    retryGame(){
      this.isGameFinished = false;
      this.hasPlayerWon = false;
      this.hasGameStarted = false;
    }
  }
}
</script>

