<template>
    <div v-if="showGame">
        <div id="guessContainer">
            <div v-if="guesses.length == 0" class="rounded-corners">{{ startNum }}</div>
            <div v-else v-for="guess in guesses" :key="guess" class="rounded-corners">{{ guess }}</div>
        </div>
        <button class="circular-button" @click="newGuess(true)">&#11014;</button>
        <button class="circular-button" @click="guessIsCorrect">&#128077;</button>
        <button class="circular-button" @click="newGuess(false)">&#x2b07;</button>
    </div>
</template>

<script>
    export default {
        props: ['showGame', 'startNum'],
        data(){
            return{
                numGuesses: 0,
                maxGuesses: 5,
                curGuess: -1,
                guesses: [],
                minMax: [0, 99]
            }
        },
        methods: {
            generateGuess(){
                return Math.floor(Math.random()*(this.minMax[1] - this.minMax[0] + 1) + this.minMax[0])
            },
            
            resetGame(){
                this.guesses.length = 0;
                this.minMax[0] = 0;
                this.minMax[1] = 99;
                this.numGuesses = 0;
                this.curGuess = -1;
            },

            finishGame(isPlayerWinner)
            {
                if(isPlayerWinner){
                    console.log("player won");
                    this.$emit('gameFinished', true);
                }
                else{
                    console.log("computer won");
                    console.log(this.curGuess);     
                    this.$emit('gameFinished', false, this.curGuess);
                }
                this.resetGame();
            },

            guessIsCorrect(){
                console.log("here");
                if(this.curGuess == -1)
                this.curGuess = this.startNum;
            
                this.finishGame(false);
            },

            newGuess(isBigger){
                console.log("new guess");
                if(((this.minMax[1] - this.curGuess) == 1) || ((this.curGuess - this.minMax[0]) == 1)){           
                    this.finishGame(false);
                }
                else if(this.numGuesses == this.maxGuesses){
                    this.finishGame(true);

                }
                else{
                    if(this.curGuess == -1)
                    {
                        this.curGuess = this.startNum;
                        this.guesses.push(this.curGuess);
                    }

                    let idx = isBigger ? 0 : 1;
                    this.minMax[idx] = this.curGuess;
                    
                    do{
                        this.curGuess = this.generateGuess();
                    }while(this.guesses.includes(this.curGuess))
                    
                    this.numGuesses += 1;
                    this.guesses.push(this.curGuess);
                }
            }
        }
    }
</script>

